import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { LOGIN_BUTTON_SIZES, LOGIN_BUTTON_TYPE, TRANSLATIONS } from './exports';

@Component({
  tag: 'mdx-login-button',
  styleUrl: 'login-button.scss',
  shadow: true,
})
export class Button {
  @Prop({ reflect: true })
  size: (typeof LOGIN_BUTTON_SIZES)[keyof typeof LOGIN_BUTTON_SIZES] = LOGIN_BUTTON_SIZES.SM;

  @Prop({ reflect: true })
  type: (typeof LOGIN_BUTTON_TYPE)[keyof typeof LOGIN_BUTTON_TYPE] = LOGIN_BUTTON_TYPE.neutral;

  @Prop({ reflect: true })
  language: keyof typeof TRANSLATIONS = 'de';

  @Prop({ reflect: true })
  dark: boolean;

  @Prop({ reflect: true })
  isLoggedIn: boolean;

  @Event()
  loginButtonClick: EventEmitter<void>;

  render() {
    return (
      <button
        class="login-button"
        type="button"
        aria-label={TRANSLATIONS[this.language].aria_label_login}
        onClick={() => this.loginButtonClick.emit()}
      >
        <mdx-icon-migros-filled class="login-button__icon" size={this.size === 'md' ? ICON_SIZES.SM : ICON_SIZES.XS} />
        {this.isLoggedIn ? TRANSLATIONS[this.language].logout : TRANSLATIONS[this.language].login}
      </button>
    );
  }
}
