:host .login-button {
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  max-width: 100%;
  text-align: center;
  text-decoration: none;
  justify-content: center;
  transition: color linear 0.2s;

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 2px;
  }

  &:hover {
    transition: var(--mdx-comp-button-transition-hover-fade-in-out);
  }

  &__icon {
    display: flex;
    align-items: center;
  }
}

:host([type='neutral'][size='xs']) .login-button {
  align-items: center;
  font: var(--mdx-comp-login-buttons-font-buttons-x-small-default);
  color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-color-default);
  background-color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-background-default);
  border: var(--mdx-comp-login-buttons-border-width-buttons-x-small-default) solid
    var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-border-default);
  border-radius: var(--mdx-comp-login-buttons-border-radius-buttons-x-small-default);
  gap: var(--mdx-comp-login-buttons-spacing-x-small-gap-logo-text);
  width: var(--mdx-comp-login-buttons-sizing-buttons-x-small-width);
  height: var(--mdx-comp-login-buttons-sizing-buttons-x-small-height);
  padding-right: var(--mdx-comp-login-buttons-spacing-x-small-padding-right);
  padding-left: var(--mdx-comp-login-buttons-spacing-x-small-padding-left);
  padding-top: var(--mdx-comp-login-buttons-spacing-x-small-padding-top);
  padding-bottom: var(--mdx-comp-login-buttons-spacing-x-small-padding-bottom);

  &:hover {
    font: var(--mdx-comp-login-buttons-font-buttons-x-small-hover);
    color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-color-hover);
    background-color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-background-hover);
    border: var(--mdx-comp-login-buttons-border-width-buttons-x-small-hover) solid
      var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-border-hover);
    border-radius: var(--mdx-comp-login-buttons-border-radius-buttons-x-small-hover);
  }

  &__icon {
    color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-icon-color-default);

    &:hover {
      color: var(--mdx-comp-login-button-color-buttons-light-neutral-x-small-icon-color-hover);
    }
  }
}

